                    </div>
                    <div class="c-text">
                      <p>However unpredictable the future may seem, the practice of foresight gives us a window into the opportunities that we see in the challenges that lie ahead of us. Be it water shortages, a frenzied climate, depleting resources or unstable governments, a day in our present-day world is  already ridden with not just unpredictable events but also complex systemic injustices that worsen their impacts. As a starting point for engaging with the complex humanitarian crises that the future holds, join us in imagining their potential impacts and thinking through how aid communities might come together to face these odds.</p>
                      <p>If you are a <span class="weight-bold">researcher, creator, or design studio keen to collaborate</span> with us on the next stages HUM.2035, a cultural institution that sees the potential in supporting <span class="weight-bold">HUM.2035 as a transmedia storytelling project</span> from South Asia, or a humanitarian organisation that sees the potential in leveraging <span class="weight-bold">HUM.2035 as a catalyst for institutional transformation</span>, do get in touch with us at <span class="underline">hello@quicksand.co.in.</span></p>
