<script>
import HumHeader from '@/components/HumHeader.vue'
import Papa from 'papaparse'
import barb from '@/static/barb.csv'

export default {
  name: 'About',
  components: {
    HumHeader,
  },
  data() {
    return {
      desktopMode: false,
      csvCol1: [],
      csvCol2: [],
      csvCol3: [],
      csvCol4: [],
      part1: {
        text: "",
        counter: 0,
        index: 0,
        el: 'part1',
        composition: ['csvCol1', 'csvCol2'],
        interval: null
      },
      part2: {
        text: "",
        counter: 0,
        index: 0,
        el: 'part2',
        composition: ['csvCol3'],
        interval: null
      },
      part3: {
        text: "",
        counter: 0,
        index: 0,
        el: 'part3',
        composition: ['csvCol4'],
        interval: null
      },
      sec: 0,
      secInterval: null,
    }
  },
  beforeRouteLeave (to, from, next) {
    var part1Interval = this.part1.interval;
    var part2Interval = this.part2.interval;
    var part3Interval = this.part3.interval;
    clearInterval(part1Interval);
    clearInterval(part2Interval);
    clearInterval(part3Interval);
    clearInterval(this.secInterval);
    next();
  },
  methods: {
    typeText(part) {
      var that = this;
      var text =  part.text.substring(0, part.index + 1);
      document.getElementById(part.el).innerHTML = text;
      part.index++;

      if(that.sec%18 == 1) {
        clearInterval(part.interval);
        setTimeout(function() {
          part.interval = setInterval(function() {that.deleteText(part);}, 50);
        }, 1000);
      }
    },
    deleteText(part) {
      var that = this;
      var text =  part.text.substring(0, part.index - 1);
      document.getElementById(part.el).innerHTML = text;
      part.index--;

      if(text == '') {
        var comp1 = part.composition[0];
        var comp2 = part.composition[1];
        part.text = that[comp1][Math.floor(Math.random() * that[comp1].length)] + (comp2 ? " " + that[comp2][Math.floor(Math.random() * that[comp2].length)] : "");

        clearInterval(part.interval);

        if(part.counter == (part.text.length - 1)) {
          part.counter = 0;
        }else {
          part.counter++;
        }

        part.index = 0;

        setTimeout(function() {
          part.interval = setInterval(function() {that.typeText(part);}, 100);
        }, 200);
      }
    },
  },
  beforeMount() {
    var that = this;
    var vw = Math.max(document.documentElement.clientWidth || 0, window.innerWidth || 0);
    if(vw < 1024) {
      that.desktopMode = false;
    }else {
      that.desktopMode = true;
    }
    window.addEventListener('resize', function() {
      var vw = Math.max(document.documentElement.clientWidth || 0, window.innerWidth || 0);
      if(vw < 1024) {
        that.desktopMode = false;
      }else {
        that.desktopMode = true;
      }
    });
  },
  mounted() {
    var that = this;
    
    var papa = Papa.parse(barb, {});
    var csvData = papa.data || [];
    for(var i = 1; i < csvData.length; i++) {
      if(csvData[i][0] && csvData[i][0].trim() != "") {
        that.csvCol1.push(csvData[i][0]);
      }
      if(csvData[i][1] && csvData[i][1].trim() != "") {
        that.csvCol2.push(csvData[i][1]);
      }
      if(csvData[i][2] && csvData[i][2].trim() != "") {
        that.csvCol3.push(csvData[i][2]);
      }
      if(csvData[i][3] && csvData[i][3].trim() != "") {
        that.csvCol4.push(csvData[i][3]);
      }
    }

    that.part1.text = that.csvCol1[Math.floor(Math.random() * that.csvCol1.length)] + " " + that.csvCol2[Math.floor(Math.random() * that.csvCol2.length)];
    that.part2.text = that.csvCol3[Math.floor(Math.random() * that.csvCol3.length)];
    that.part3.text = that.csvCol4[Math.floor(Math.random() * that.csvCol4.length)];

    that.sec = new Date().getSeconds();

    that.part1.interval = setInterval(function(){that.typeText(that.part1)}, 100);
    that.part2.interval = setInterval(function(){that.typeText(that.part2)}, 100);
    that.part3.interval = setInterval(function(){that.typeText(that.part3)}, 100);
    that.secInterval = setInterval(function() {that.sec = new Date().getSeconds()}, 16);

  }
}
</script>

